@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
@font-face {
  font-family: "RetroGaming";
  src: local("RetroGaming"),
   url("./font/RetroGaming.ttf") format("truetype");
  font-weight: bold;
  }
button {
  font-family: RetroGaming;
}
.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
}
::-webkit-scrollbar {
  display: none;
}
.App-header {
  background-color: #f99321;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.container {
  width: 95vw;
  height: 95vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
}

.square-logo{
  border: #000000;
  width: 150px;
  height: 150px;
  border: 10px solid #000000;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0;
  transition: 1s;
  display: flex;
  align-items: center;
  align-content: left;
}

.square-logo button{
  padding-right: 100px;
  text-transform: uppercase;
  background-color: transparent;
  border: 0px;
  transition: 0.5s;
  font-size: 0px;
  opacity: 100%;
  font-weight: bold;
}

.square-logo:hover button{
  background-color: transparent;
  margin: 18px;
  margin-right: 300px;
  width: 170px;
  height: 50px;
  text-transform: uppercase;
  border: 0px;
  transition: 0.5s;
  font-size: 30px;
  opacity: 100%;
  font-weight: bold;
}

.square-logo button:hover{
  background-color: #000000;
  border-radius: 10px;
  color: #f99321;
}
.square-logo ul li{
  background-color: rgba(0, 0, 0, 0);
  z-index: 0;
  position: relative;
  list-style: none;
  list-style-position: outside;
  line-height: 10px;
}
.square-logo:hover li{
  line-height: 0px;
}
.square-logo h1{
  font-family: RetroGaming;
  z-index: 2;
  padding-top: 190px;
  padding-left: 6px;
  color: #000000;
  height: 30px;
  font-size: 30px;
  position: absolute;
  transition: 1s;
}
.square-logo:hover h1{
  margin-top: -400px;
  padding-top: 0px;
  padding-left: 70px;
  font-size: 40px;
  opacity: 1;
  transition: 1s;
}
.square-logo:hover{
  border: #000000;
  width: 300px;
  height: 300px;
  border: 15px solid #000000;
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
  transition: 1s;
}

@media (prefers-reduced-motion:no-preference){
  .square-logo{
    -webkit-animation:App-logo-spin 0.3s 1;
    animation:App-logo-spin 0.3s 1;
  }
}
@-webkit-keyframes App-logo-hover-spin{
  0% {
    -webkit-transform:rotate(-90deg);
    transform:rotate(-90deg)
  }
  to {
    -webkit-transform:rotate(0deg);
    transform:rotate(0deg)
  }
}
@keyframes App-logo-hover-spin{
  0% {
    -webkit-transform:rotate(-360deg);
    transform:rotate(-360deg)
  }
  to {
    -webkit-transform:rotate(0deg);
    transform:rotate(0deg)
  }
}

@-webkit-keyframes App-logo-spin{
  0% {
    -webkit-transform:rotate(-90deg);
    opacity: 0%;
    transform:rotate(-90deg)
  }
  to {
    -webkit-transform:rotate(0deg);
    opacity: 100%;
    transform:rotate(0deg)
  }
}
@keyframes App-logo-spin{
  0% {
    -webkit-transform:rotate(-90deg);
    opacity: 0%;
    transform:rotate(-90deg)
  }
  to {
    -webkit-transform:rotate(0deg);
    opacity: 100%;
    transform:rotate(0deg)
  }
}
.square-frame {
  align-items: center;
  text-align: center;
  float: left;
  background-color: #000;
  width: 100%;
  height: 100vmin;
  color: #f99321;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#frame-EPTC {
  background-image: url("img/eptcpicture.png");
}
#frame-cardgame {
  background-image: url("img/cardgamepicture.jpeg");
}
#frame-pineapple {
  background-image: url("img/pineapplepicture.png");
}
#frame-ritmo {
  background-image: url("img/eptcpicture.png");
}
.square-frame h1 {
  position: relative ;
  margin-top: 5%;
  background-color: #020201;
  width: fit-content;
  padding: 10px;
  color: #000;
  margin-left: auto;
  margin-right: auto;
  float: center;
  border-radius: 10px;
}
.square-frame .about-description {
  position: inherit;
  border-radius: 15px;
  background-color: #f99321;
  margin-top: 15%;
  margin-left: 10%;
  width: 40%;
  padding: 10px;
  color: #000000;
  font-size: large;
  text-align: justify;
  z-index: 2;
}
.square-frame button{
  margin-top: 30px;
  padding: 15px;
  font-size: 24px;
  text-align: center;
  cursor: pointer;
  outline: none;
  color: #000;
  background-color: #f99321;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px rgb(75, 75, 75);
}
.square-frame button:active {
  box-shadow: 0 5px rgb(75, 75, 75);
  transform: translateY(4px);
}
.square-frame .prev-button {
  position: absolute;
  margin-top: 14%;
  margin-left: -30%;
  padding: 15px;
  font-size: 24px;
  text-align: center;
  cursor: pointer;
  outline: none;
  color: #000;
  background-color: #f99321;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px rgb(83, 83, 83);
}
.square-frame .prev-button:active {
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}
.square-frame .next-button {
  position: absolute;
  margin-top: 14%;
  margin-left: 15%;
  padding: 15px;
  font-size: 24px;
  text-align: center;
  cursor: pointer;
  outline: none;
  color: #000;
  background-color: #f99321;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px rgb(83, 83, 83);
}
.square-frame .next-button:active {
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

.square-frame .special-button {
  position: absolute;
  margin-top: 14%;
}
.game-title {
  margin-top: -5%;
  margin-bottom: auto;
  width: 100%;
  background-color: #f99321;
}
.game-title h1{
  background-color: #f99321;
  color: #000;
}